<template>
  <div
    :class="[ISXINWEI ? 'login_container_xw' : 'login_container']"
    @keyup.enter="handleLoginEvent"
  >
    <div class="login_box">
      <div :class="login_box()">
        <!-- <p v-if="ISXINWEI" style="margin: 0;font-size: 36px;font-family: ui-monospace;font-weight: bold;" >信维通信SCADA系统</p> -->
        <p
          style="margin: 0;font-size: 36px;font-family: ui-monospace;font-weight: bold;"
        >
          数据采集系统
        </p>
        <!-- <img class="lzLogo" :src="require('../assets/img/Login/logo.png')"  /> -->
        <!-- <img v-show="showLogo()" :src="require('../assets/img/Login/login_icon_Shattrath.png')" alt="沙塔信息技术有限公司" />
          -->
        <!-- <img :class="logoClass()" :src="logoImg()"  /> -->
      </div>
      <div class="margin-bottom-30">
        <h1>用户登录</h1>
      </div>
      <div class="margin-bottom-20">
        <el-input
          ref="accountInput"
          class="width-360 login-input"
          suffix-icon="login-account"
          placeholder="用户名"
          v-model.trim="loginAccount"
          clearable
        ></el-input>
      </div>
      <div class="margin-bottom-15">
        <el-input
          type="password"
          ref="passwordInput"
          class="width-360 login-input"
          suffix-icon="login-password"
          placeholder="密码"
          v-model.trim="loginPassword"
          clearable
        ></el-input>
      </div>
      <div class="margin-bottom-40 display-grid">
        <!-- <el-checkbox class="justify-self-start" v-model="isPasswordKept">记住密码</el-checkbox> -->
        <div class="justify-self-start">
          <el-link @click="toRegister">立即注册</el-link>
        </div>
        <!-- <div class="justify-self-end">
          <el-link>忘记密码？</el-link>
        </div> -->
      </div>
      <div class="btnClass">
        <el-button class="width-360" type="primary" @click="handleLoginEvent"
          >立即登录</el-button
        >
        <!-- <el-button class="width-360" type="text" @click="toCaptchaLogin">短信验证码登录</el-button> -->
      </div>
    </div>
  </div>
</template>

<script>
// eslint-disable-next-line camelcase
// import Crypto from '@/utils/ase.js';
// eslint-disable-next-line camelcase
import { IS_Mk, LOGO, ROUTER_CONFIG, ISXINWEI } from '@/utils/ajaxConfigure';
import { showErrorNotify } from '@/utils/notify';
import { STATUS_CODE, TITLE } from '@/utils/constantsFile';
// 导入所需接口
import {
  login
  // logout
} from '@/api/public';
// import { showErrorMessage } from '@/utils/message';
export default {
  name: 'Login',

  data() {
    return {
      // 用户名
      loginAccount: '',

      // 密码
      loginPassword: '',

      // 是否记住密码
      isPasswordKept: true,
      ISXINWEI
    };
  },

  methods: {
    login_box() {
      if (LOGO === 'null') {
        return 'margin-top-50-x';
      } else {
        return 'margin-top-50';
      }
    },
    logoImg() {
      if (LOGO === 'null') {
        return '';
      } else {
        return require('../assets/img/Login/login_' + LOGO + '.png');
      }
    },
    logoClass() {
      if (LOGO === 'SUPOS') {
        return 'lzLogo';
      } else if (LOGO === 'YIY') {
        return 'ylLogo';
      } else if (LOGO === 'BY') {
        return 'byLogo';
      } else {
        return '';
      }
    },
    showLogo() {
      // eslint-disable-next-line camelcase
      if (IS_Mk) {
        return false;
      }
      if (localStorage.getItem('isSupplier') === 'true') {
        return false;
      } else {
        return true;
      }
    },
    /**
     * @description: “立即登录”按钮点击事件
     * @param 无
     * @return: void
     * @author: cyb
     */
    handleLoginEvent() {
      // 提示用户名不能为空
      if (!this.loginAccount) {
        this.$message({
          showClose: true,
          message: '用户名不能为空！',
          type: 'warning'
        });
        this.$refs.accountInput.focus();
        return false;
      }

      // 提示密码不能为空
      if (!this.loginPassword) {
        this.$message({
          showClose: true,
          message: '密码不能为空！',
          type: 'warning'
        });
        this.$refs.passwordInput.focus();
        return false;
      }
      this.login();
    },
    async login() {
      try {
        var loginTime = new Date().getTime();
        var key = loginTime.toString().substring(4, 14) + '1234567';
        console.log(loginTime, key);
        this.tableLoading = true;
        const res = await login(
          {},
          {
            userName: this.loginAccount,
            password: this.loginPassword
            // password: Crypto.encryptDes2(this.loginPassword, key, '12345678abcdefgh'),
            // password: Crypto.encryptDes2(this.loginPassword, key, '12345678abcdefgh'),
            // loginTime: loginTime
          }
        );
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          localStorage.setItem('token', JSON.stringify(res.rows.token));
          localStorage.setItem('realName', JSON.stringify(res.rows.realName));
          localStorage.setItem('userId', JSON.stringify(res.rows.userId));
          localStorage.setItem('account', JSON.stringify(res.rows.userName));
          localStorage.setItem('company', res.rows.companyName);
          localStorage.setItem('companyId', res.rows.companyId);
          localStorage.setItem('mesToken', res.rows.mesToken);
          localStorage.setItem('mesTokenHeader', res.rows.mesTokenHeader);
          localStorage.setItem('isBoss', res.rows.isBoss);
          var url = '/';
          if (ROUTER_CONFIG[localStorage.getItem('companyId')]) {
            url = ROUTER_CONFIG[localStorage.getItem('companyId')].url;
          }
          this.$router.push(url);
        } else {
          // 提示异常信息
          this.$message({
            showClose: true,
            message: res.message,
            type: 'warning'
          });
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    },
    toCaptchaLogin() {
      this.$router.push({
        path: '/CaptchaLogin'
      });
    },
    toRegister() {
      this.$router.push({
        path: '/register'
      });
    },
    // 清除token
    clearToken() {
      const Token = window.localStorage.getItem('token');
      if (Token) {
        window.localStorage.removeItem('token');
      }
      // const Token = window.localStorage.getItem('token');
      // Token && logout({
      //   token: Token.replace(/"/g, '')
      // }).then(res => {
      //   if (res.code === '0000') {
      //     window.localStorage.removeItem('token');
      //   }
      // });
    }
  },
  created() {
    this.clearToken();
  },
  mounted() {
    // 用户名输入框自动获取焦点
    this.$refs.accountInput.focus();
  }
};
</script>

<style lang="less" scoped>
.login_container {
  // background-image: url('~@/assets/img/background/2.png');
  // background-size: 100% 100%;
  // background-repeat: no-repeat;
  background-color: #0871dc;
  height: 100%;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.login_container_xw {
  background-image: url('~@/assets/img/background/2.png');
  background-size: 100% 100%;
  background-repeat: no-repeat;
  height: 100%;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.login_box {
  width: 450px;
  height: 450px;
  background-color: #fff;
  border-radius: 10px;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
}
.login_box_null {
  width: 450px;
  height: 450px;
  background-color: #fff;
  border-radius: 10px;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
}

.margin-top-50 {
  margin-top: 44px;

  font-size: 50px;
  color: #0871dc;
  font-family: fantasy;
  text-align: center;
}
.margin-top-50-x {
  margin-top: 25px;

  font-size: 50px;
  color: #0871dc;
  font-family: fantasy;
  text-align: center;
}

.width-360 {
  width: 360px;
}

.margin-bottom-40 {
  margin-bottom: 40px;
}

.margin-bottom-30 {
  margin-bottom: 20px;
}

.margin-bottom-20 {
  margin-bottom: 20px;
}

.margin-bottom-15 {
  margin-bottom: 15px;
}

.display-grid {
  display: grid;
  grid-template-columns: 180px 180px;
}

// “记住密码”checkbox靠左对齐
.justify-self-start {
  justify-self: start;
  .el-link.el-link--default {
    color: #66b1ff;
  }
}

// “忘记密码”链接靠右对齐
.justify-self-end {
  justify-self: end;
}

// 调整输入框中的icon的位置
.login-input /deep/ .el-input__icon {
  margin-top: 4px;
}

.btnClass {
  text-align: center;

  /deep/ .el-button {
    padding: 12px 20px;
  }
}

.lzLogo {
  width: 55%;
}
.ylLogo {
  width: 35%;
}

.byLogo {
  width: 55%;
}
</style>
